.musica__container{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: auto;
    box-shadow: 0px 2px 19px $black-gentle;
    background-color: $dark-gray;
    color: $white-styled;

    h1{
        margin-bottom: 3rem;
        margin-top: 3rem;
        font-size: 2rem;
        padding: 1rem;
    }
}
.musica__frames{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 90%;
    justify-content: space-around;
    align-items: center;
    padding: 1rem;
}
.musica__spotify{
    width: 40%;
    border-radius: 12px;
    box-shadow: 0px 0px 10px $white-styled;
}
.musica__soundcloud{
    width: 40%;
    border-radius: 12px;
    box-shadow: 0px 0px 10px $white-styled;

}

@media (max-width: 800px) {
    .musica__frames{
        flex-direction: column;

    }
    .musica__spotify{
        width: 90%;
        margin-bottom: 1rem;

    }
    .musica__soundcloud{
        width: 90%;}
    
}
@media (max-width: 600px) {
   
    .musica__frames{
        
        margin-right: 100px !important;
        margin-left: 0px !important;
    }
    .musica__container{
        h1{
            margin-right: auto !important;
        }
    }

}